import { createUseStyles } from "react-jss";
export default function HomeAnalytics() {
  const styles = useStyles();
  return (
    <div className={styles.container} >
      <div className={styles.header} >
        <p className={styles.heading} >Kes 2000</p>
        <p className={styles.subHeading} >This Week's Spending</p>
        
      </div>
    </div>
  );
}
const useStyles = createUseStyles({
    container:{
        width:"48%",
        margin:"10px"
    },
    header:{
        marginBottom:"10px",
    },
    heading:{
        fontWeight:"800",
        fontFamily:"Poppins"
    },
    subHeading:{
        fontFamily:"Poppins",
        fontSize:"12px"
    }
})
