import { createUseStyles } from "react-jss";
export default function RecentRow() {
  const styles = useStyles();
  return (
    <div className={styles.tableRow}>
      <img
        src="https://img.icons8.com/fluency/512/paid-bill.png"
        alt="icon"
        className={styles.tableIcon}
      />
      <p className={styles.tableHeading}>Rent</p>
      <p className={styles.tableAmount}>-KES 10,000</p>
      <p className={styles.tableDay}>Yesterday</p>
    </div>
  );
}

const useStyles = createUseStyles({
  container: {
    margin: "10px",
  },
  headerRow: {
    display: "flex",
    justifyContent: "space-between",
  },
  heading: {
    fontWeight: "800",
    fontFamily: "Poppins",
  },
  link: {
    color: "#12B886",
    fontFamily: "Poppins",
    fontWeight: "700",
    fontSize: "12px",
  },
  tableRow: {
    display: "flex",
    justifyContent: "space-between",
    alignContent: "center",
    alignItems: "center",
    marginTop: "20px",
  },
  tableIcon: {
    marginRight: "40px",
    width: "50px",
    height: "50px",
  },
  tableHeading: {
    fontWeight: "800",
    fontFamily: "Poppins",
    fontSize: "14px",
    marginRight: "40px",
  },
  tableAmount: {
    marginRight: "40px",
    fontFamily: "Poppins",
    fontSize: "14px",
  },
  tableDay: {
    marginRight: "40px",
    fontFamily: "Poppins",
    fontSize: "14px",
  },
});
